<script setup lang="ts">

import { useStore } from 'vuex';
import { computed, onMounted } from 'vue';
import { SliderCards } from '@/features/slider-cards'
const store = useStore();

onMounted(() => {
    store.dispatch('products/fetchProducts');
});

const products = computed(() => store.state.products.productsData);

</script>

<template>
    <SliderCards v-for="product in products" :key="product.id" :cardData="product" />
</template>
<style lang="scss" scoped>
.product-item {
    .item-content {
        .image {
            display: block;
            width: 250px;
            height: 400px;
        }
    }
}
</style>